import React from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { Button, Form, Card } from 'react-bootstrap'
import toast from 'react-hot-toast'
import Loading from '../../common/Loading'
import Error from '../../common/Error'
import NoContent from '../../common/NoContent'
import ImageSearchModal from '../ImageSearchModal'
import defaultBookCover from '../../images/placeholder/default-book-cover.png'
import urls from '../../settings/urls'
import { booksitoutServer } from '../../functions/axios'
import RouteTitle from '../../common/RouteTitle';
import RouteContainer from '../../common/RouteContainer'
import booksitoutIcon from '../../common/icons/booksitoutIcon';

const BookEditForm = () => {
	const { id } = useParams()
	const navigate = useNavigate()

	const TITLE_MESSAGE = `   `
	const AUTHOR_MESSAGE = `   `
	const PAGE_MESSAGE = `   `
	const EDIT_SUCCESS_MESSAGE = ` `

	const [initalFetch, setInitialFetch] = React.useState(true)
	const [loading, setIsLoading] = React.useState(true)
	const [error, setIsError] = React.useState(false)
	const [notFound, setNotFound] = React.useState(false)

	const [showModal, setShowModal] = React.useState(false)
	const openModal = () => {
		title !== '' ? setShowModal(true) : alert('      ')
	}

	const [title, setTitle] = React.useState('')
	const [author, setAuthor] = React.useState('')
	const [endPage, setEndPage] = React.useState(0)
	const [cover, setCover] = React.useState('')
	const [language, setLanguage] = React.useState('KOREAN')
	const [category, setCategory] = React.useState('LITERATURE')
	const [sharing, setSharing] = React.useState<boolean>(true)
	const [form, setForm] = React.useState('PHYSICAL')
	const [source, setSource] = React.useState('NOT_PROVIDED')

	React.useEffect(() => {
		const BOOK_GET_API_URL = `${urls.api.base}/v1/book/${id}`

		setTimeout(() => {
			setInitialFetch(false)
		}, 5000)

		booksitoutServer
			.get(BOOK_GET_API_URL)
			.then(res => {
				if (!res.status.toString().startsWith('2')) {
					setNotFound(true)
					return
				}

				return res.data
			})
			.then(book => {
				setTitle(book.title)
				setAuthor(book.author)
				setLanguage(book.language)
				setCategory(book.category)
				setEndPage(book.endPage)
				setForm(book.form)
				setSource(book.source)
				setCover(book.cover)
				setSharing(book.sharing)
			})
			.catch(() => setIsError(true))
			.finally(() => {
				setIsLoading(false)
				setInitialFetch(false)
			})
	}, [id])

	const handleEdit = (e) => {
		e.preventDefault()

		const editedBook = {
			title: title,
			author: author,
			language: language,
			category: category,
			endPage: endPage,
			form: form,
			soruce: source,
			cover: cover,
			isSharing: sharing,
		}

		booksitoutServer
			.put(`/v1/book/${id}`, editedBook)
			.then(() => {
				toast.success(EDIT_SUCCESS_MESSAGE)
				navigate(`/book/detail/${id}`)
			})
			.catch(() => toast.error('   .     '))
	}

	if (initalFetch) return <></>
	if (loading) return <Loading />
	if (error)return <Error />
	if (notFound) return <NoContent />

	return (
		<RouteContainer>
			<RouteTitle icon={<booksitoutIcon.book />} title={' '} />

			<ImageSearchModal
				showModal={showModal}
				setShowModal={setShowModal}
				setCover={setCover}
				title={title}
				author={author}
			/>

			<Card>
				<Card.Body>
					<Form onSubmit={handleEdit} className="h-100 container mt-3 mb-3 text-start">
						<div className="row row-eq-height text-center">
							<div className="col-12 col-lg-4 mb-3 mt-md-4">
								<img
									src={cover === '' ? defaultBookCover : cover}
									alt=""
									className="img-fluid rounded border"
								/>
							</div>

							<div className="col-12 col-lg-8 mb-3 text-start">
								<div className="row">
									<div className="col-12 col-md-6">
										<Form.Group className="mb-3">
											<Form.Label> </Form.Label>
											<Form.Control
												type="text"
												placeholder={TITLE_MESSAGE}
												required
												onChange={e => setTitle(e.target.value)}
												value={title}
											/>
										</Form.Group>
									</div>

									<div className="col-12 col-md-6">
										<Form.Group className="mb-3">
											<Form.Label></Form.Label>
											<Form.Control
												type="text"
												placeholder={AUTHOR_MESSAGE}
												onChange={e => setAuthor(e.target.value)}
												value={author}
											/>
										</Form.Group>
									</div>
								</div>

								<Button
									className="w-100 mt-2 mb-3"
									onClick={openModal}
									variant={title !== '' ? 'book' : 'secondary'}
								>
									  
								</Button>

								<hr />

								<div className="row">
									<div className="col-6 col-md-4">
										<Form.Group className="mb-3">
											<Form.Label> </Form.Label>
											<Form.Select value={language} onChange={e => setLanguage(e.target.value)}>
												<option value="KOREAN"> </option>
												<option value="ENGLISH"> </option>
												<option value="JAPANESE"> </option>
												<option value="CHINESE"> </option>

												<option value="FRENCH"> </option>
												<option value="SPANISH">  </option>
											</Form.Select>
										</Form.Group>
									</div>

									<div className="col-6 col-md-4">
										<Form.Group>
											<Form.Label></Form.Label>
											<Form.Select onChange={e => setCategory(e.target.value)} value={category}>
												<option value="LITERATURE"></option>

												<option value="NATURAL_SCIENCE"></option>
												<option value="SOCIAL_SCIENCE"></option>
												<option value="TECHNOLOGY"></option>

												<option value="PHILOSOPHY"></option>
												<option value="LANGUAGE"></option>
												<option value="ART"></option>
												<option value="HISTORY"></option>

												<option value="RELIGION"></option>

												<option value="OTHERS"></option>
											</Form.Select>
										</Form.Group>
									</div>

									<div className="col-6 col-md-4">
										<Form.Group className="mb-3">
											<Form.Label>  </Form.Label>
											<Form.Control
												type="number"
												inputMode="numeric"
												pattern="[0-9]*"
												placeholder={PAGE_MESSAGE}
												required
												autoComplete="off"
												value={endPage}
												onChange={e => setEndPage(Number(e.target.value))}
											/>
										</Form.Group>
									</div>

									<div className="col-6">
										<Form.Group className="mb-3">
											<Form.Label> </Form.Label>
											<Form.Select value={form} onChange={e => setForm(e.target.value)}>
												<option value="PHYSICAL"> </option>
												<option value="EBOOK"> </option>
												<option value="AUDIO"> </option>
											</Form.Select>
										</Form.Group>
									</div>

									<div className="col-12 col-md-6">
										<Form.Group className="mb-3">
											<Form.Label>  ?</Form.Label>
											<Form.Select value={source} onChange={e => setSource(e.target.value)}>
												<option value="NOT_PROVIDED">  </option>

												<option value="BUY_NEW_OFFLINE">  -  </option>
												<option value="BIY_NEW_ONLINE">  -  </option>
												<option value="BUY_USED_OFFLINE">
													 -   ( )
												</option>
												<option value="BUY_USED_ONLINE"> -  </option>

												<option value="LIBRARY"></option>
												<option value="BORROW_STORE">  </option>
												<option value="BORROW_FRIENDS"> </option>

												<option value="SUBSCRIPTION"> (  )</option>

												<option value="OTHERS"></option>
											</Form.Select>
										</Form.Group>
									</div>
								</div>

								<Form.Group className="mb-5" controlId="formBasicCheckbox">
									<Form.Check
										type="switch"
										label="   "
										checked={sharing}
										onChange={() => setSharing(!sharing)}
										className="force-1-line"
									/>
								</Form.Group>

								<div className="row justify-content-center">
									<div className="col-12 col-md-6 mb-2 mb-md-0">
										<Button variant="book-danger" type="reset" className="w-100">
											 
										</Button>
									</div>

									<div className="col-12 col-md-6 mb-2 mb-md-0">
										<Button variant="book" type="submit" className="w-100">
											
										</Button>
									</div>
								</div>
							</div>
						</div>
					</Form>
				</Card.Body>
			</Card>
		</RouteContainer>
	)
}

export default BookEditForm
