import React from 'react'
import RouteContainer from '../common/RouteContainer'
import RouteTitle from '../common/RouteTitle'
import booksitoutIcon from '../common/icons/booksitoutIcon';
import styled from 'styled-components';
import EditorForm from '../common/editor/EditorForm';
import toast from 'react-hot-toast';
import { Button, Card, Container, Form } from 'react-bootstrap';
import { booksitoutServer } from '../functions/axios';
import { useNavigate } from 'react-router-dom';
import SuggestionModal from './SuggestionModal';

const SuggestionRoute = () => {
	const navigate = useNavigate()

	const [rating, setRating] = React.useState<'GOOD' | 'NEUTRAL' | 'BAD'>('GOOD')
	const [content, setContent] = React.useState<string>('')

	const [show, setShow] = React.useState<boolean>(true)

	const handleSubmit = (e) => {
		e.preventDefault()

		if (content === '') {
			toast.error('  !')
			return
		}

		const suggestion = {
			rating: rating,
			content: content
		}

		booksitoutServer
			.post('/v6/user/suggestion', suggestion)
			.then(() => {
				toast.success(' /  .     :)')
				navigate('/')
			})
			.catch(() => {
				toast.error(
					' !         . booksitout@gmail.com',
				)
			})
	}

	return (
		<Container>
			<RouteTitle
				icon={<booksitoutIcon.suggestion />}
				title={' / '}
				subTitle="   ,     "
			/>

			<SuggestionModal show={show} setShow={setShow} />

			<FormContainer>
				<FormCard>
					<Card.Body>
						<Form onSubmit={handleSubmit}>
							<FormLabelContainer>
								<Form.Label> ?</Form.Label>
								<Form.Select
									onSelect={e => setRating(e.currentTarget.value as 'GOOD' | 'NEUTRAL' | 'BAD')}
								>
									<option value={'GOOD'}></option>
									<option value={'NEUTRAL'}></option>
									<option value={'BAD'}></option>
								</Form.Select>
							</FormLabelContainer>

							<FormLabelContainer>
								<Form.Label>   </Form.Label>
								<EditorForm placeholder={'  '} setContent={setContent} />
							</FormLabelContainer>

							<FormLabelContainer>
								<div className="row justify-content-center">
									<div className="col-12 col-md-6">
										<Button variant="book" className="w-100" type="submit">
											 
										</Button>
									</div>
								</div>
							</FormLabelContainer>
						</Form>
					</Card.Body>
				</FormCard>
			</FormContainer>
		</Container>
	)
}

const FormContainer = styled.div.attrs({
	className: 'mt-4',
})``

const FormLabelContainer = styled.div.attrs({
	className: 'mt-3',
})``

const FormCard = styled(Card).attrs({
	className: '',
})``

export default SuggestionRoute