import React from "react"
import axios from "axios"
import toast from "react-hot-toast"
import utils from "../../functions/utils"
import messages from "../messages"
import { ButtonGroup, ToggleButton } from "react-bootstrap"
import urls from "../urls"
import SettingsCard from "../SettingsCard"

const LibrarySearchMethodSettings = () => {
	const [currentApiKey, setCurrentApiKey] = React.useState(localStorage.getItem('library-search-method') || 'REGION')

	const settingsList = [
		{
			key: 1,
			label: '',
			apiKey: 'REGION',
		},
		{
			key: 2,
			label: ' ',
			apiKey: 'SPECIFIC',
		},
	]

	const changeSetting = (apiKey: string) => {
		if (apiKey === currentApiKey) {
			toast.error(apiKey === 'REGION' ? '   ' : '    ')
			return
		}

		axios
			.put(
				`${urls.api.base}/v3/search/settings/offline-library/search-method`,
				{ method: apiKey },
				{ headers: { Authorization: utils.getToken() } }
			)
			.then((res) => {
				if (res.status === 200) {
					if (apiKey === 'REGION') {
						toast.success('   ')
					}

					if (apiKey === 'SPECIFIC') {
						toast.success('    ')
					}

					localStorage.setItem('library-search-method', apiKey)
					setCurrentApiKey(apiKey)
				} else {
					toast.error(messages.error)
				}
			})
			.catch(() => {
				toast.error(messages.error)
			})
	}

	return (
		<SettingsCard
			title=' '
			content={
				<div className='row justify-content-center'>
					<div className='ms-md-5 mb-5'>
						<p className='text-secondary mb-3 mb-md-2'>            </p>
						<p className='text-secondary mb-3 mb-md-2'> 1   </p>
					</div>

					<ButtonGroup className='col-12 col-md-8 col-xl-6' style={{ position: 'absolute', bottom: '20px' }}>
						{settingsList.map((setting) => {
							return (
								<ToggleButton
									className='w-100'
									value={setting.apiKey}
									key={setting.key}
									type='radio'
									checked={false}
									onClick={() => changeSetting(setting.apiKey)}
									variant={setting.apiKey === currentApiKey ? 'book' : 'outline-book'}>
									{setting.label}
								</ToggleButton>
							)
						})}
					</ButtonGroup>
				</div>
			}
		/>
	)
}

export default LibrarySearchMethodSettings