import React from 'react'
import { Button, Card, Form, FormGroup } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import { RootState } from '../../redux/store'
import Error from '../../common/Error';
import { booksitoutServer } from '../../functions/axios';
import toast from 'react-hot-toast';
import { useNavigate } from 'react-router-dom';
import ScrollToTop from '../../common/topnav/ScrollToTop';

const AddGatheringForm = () => {
    const navigate = useNavigate()
    const isLogin = useSelector((state: RootState) => state.user.isLogin)

    const [title, setTitle] = React.useState<string>('')
    const [content, setContent] = React.useState<string>('')
    const [gatheringType, setGatheringType] = React.useState<string>('FREE')
    const [capacity, setCapacity] = React.useState<number | null>(null)
    const [locationType, setLocationType] = React.useState<string>('SUBWAY')
    const [locationDetail, setLocationDetail] = React.useState<string>('')

    const handleAddGathering = (e) => {
        e.preventDefault()

        if (title === '') {
			toast.error(' ')
			document.getElementById('title-input')!!.focus()
			return
		}

		if (content === '') {
			toast.error(' ')
			document.getElementById('content-input')!!.focus()
			return
		}

        if (content.length < 10) {
            toast.error('10    ')
            document.getElementById('content-input')!!.focus()
            return
        }

        if (capacity === null) {
			toast.error('  ')
			document.getElementById('capacity-input')!!.focus()
			return
		}

        if (locationDetail === '') {
            toast.error(' ')
            document.getElementById('location-detail-input')!!.focus()
            return
        }

        const gathering = {
			title: title,
			content: content.replaceAll('\n', '<br>'),
			type: gatheringType,
			locationType: locationType,
			location: locationDetail,
			capacity: capacity,
		}

        booksitoutServer
			.post('/v4/forum/gathering', gathering)
			.then((res) => {
				toast.success(' ')
				navigate(`/community/gathering/detail/${res.data.id}`)
			})
			.catch(() => toast.error(' .     '))
    }

    if (!isLogin) return <Error message="     " move={0} />

    return (
		<Card style={{ minHeight: '500px' }}>
			<ScrollToTop />

			<Card.Body>
				<Form onSubmit={handleAddGathering}>
					<FormGroup className="mt-3">
						<Form.Label></Form.Label>
						<Form.Control id="title-input" onChange={e => setTitle(e.target.value)} />
					</FormGroup>

					<FormGroup className="mt-3">
						<Form.Label></Form.Label>
						<Form.Control
							as="textarea"
							id="content-input"
							onChange={e => setContent(e.target.value)}
							rows={12}
						/>
					</FormGroup>

					<FormGroup>
						<div className="row">
							<div className="col-12 col-md-6 mt-3">
								<Form.Label> </Form.Label>
								<Form.Select onChange={e => setGatheringType(e.target.value)}>
									<option value="FREE"></option>
									<option value="CHECKING"> </option>
									<option value="TALKING"> </option>
									<option value="DISCUSSION"> </option>
									<option value="BOOK_REPORT"> </option>
								</Form.Select>
							</div>

							<div className="col-12 col-md-6 mt-3">
								<Form.Label> ( )</Form.Label>
								<Form.Control
									type="number"
									inputMode="numeric"
									pattern="[0-9]*"
									id="capacity-input"
									onChange={e => setCapacity(Number(e.target.value))}
									placeholder="2 ~ 10"
								/>
							</div>
						</div>
					</FormGroup>

					<FormGroup>
						<div className="row">
							<div className="col-12 col-md-6 mt-3">
								<Form.Label> </Form.Label>
								<Form.Select onChange={e => setLocationType(e.target.value)}>
									<option value="SUBWAY"></option>
									<option value="ONLINE"></option>
									<option value="OTHERS"></option>
								</Form.Select>
							</div>

							<div className="col-12 col-md-6 mt-3">
								<Form.Label> </Form.Label>
								<Form.Control
									id="location-detail-input"
									onChange={e => setLocationDetail(e.target.value)}
								/>
							</div>
						</div>
					</FormGroup>

					<div className="row justify-content-center mt-5">
						<div className="col-12 col-md-7">
							<Button type="submit" variant="book" className="w-100">
								  
							</Button>
						</div>
					</div>
				</Form>
			</Card.Body>
		</Card>
	)
}

export default AddGatheringForm