import React from 'react'
import { Modal, Button, Form } from 'react-bootstrap'
import toast from 'react-hot-toast'
import user from '../user/user'
import messages from '../settings/messages'

const EditPasswordModal = ({ show, setShow }) => {
	const [oldPassword, setOldPassword] = React.useState<string>('')
	const [newPassword, setNewPassword] = React.useState<string>('')
	const [verificationCode, setVerificationCode] = React.useState<string>('')
	const [isVerificationRequested, setIsVerificationRequested] = React.useState<boolean>(false)

	const handleRequestVerificationCode = () => {
		toast.loading('  ')

		user.change.verificationCode.password().then((success) => {
			if (success) {
				toast.success(' ')
				setIsVerificationRequested(true)
			} else {
				toast.error(messages.error)
			}
		})
	}

	const handleChangePassword = (e) => {
		e.preventDefault()

		if (!isVerificationRequested) {
			toast.error('   ')
			return
		}

		if (verificationCode === '') {
			toast.error('  ')
			return
		}

		if (verificationCode.length < 5) {
			toast.error('  ')
			return
		}

		if (oldPassword === '') {
			toast.error('   ')
			return
		}

		if (newPassword === '') {
			toast.error('   ')
			return
		}

		if (oldPassword === newPassword) {
			toast.error('    ')
			return
		}

		if (newPassword.length < 6) {
			toast.error('6    ')
			return
		}

		toast.loading('  ')

		user.change.password(verificationCode, oldPassword, newPassword).then((status) => {
			if (status.startsWith('2')) {
				toast.success(' ')
				closeModal()
			} else if (status === '400') {
				toast.error('  .   ')
			} else {
				toast.error(messages.error)
			}
		})
	}

	const closeModal = () => {
		setOldPassword('')
		setNewPassword('')
		setVerificationCode('')
		setIsVerificationRequested(false)
		setShow(false)
	}

	return (
		<Modal show={show} onHide={() => closeModal()} fullscreen='md-down' backdrop='static' centered>
			<Modal.Header className='text-center' closeButton>
				<h4 className='w-100'> </h4>
			</Modal.Header>

			<Modal.Body>
				<Form onSubmit={(e) => handleChangePassword(e)}>
					<Form.Control
						className='mb-2'
						placeholder='  '
						onChange={(e) => setVerificationCode(e.target.value)}
						disabled={!isVerificationRequested}
					/>
					<Button className='mb-3 w-100' variant='book' onClick={() => handleRequestVerificationCode()} disabled={isVerificationRequested}>
						 
					</Button>

					<Form.Control
						type='password'
						className='mb-2'
						placeholder='   '
						onChange={(e) => setOldPassword(e.target.value)}
					/>
					<Form.Control
						type='password'
						className='mb-2'
						placeholder='   '
						onChange={(e) => setNewPassword(e.target.value)}
					/>

					<div className='row mt-4'>
						<div className='col-12 col-md-6'>
							<Button className='mt-2 w-100' variant='book-danger' onClick={() => setShow(false)}>
								
							</Button>
						</div>

						<div className='col-12 col-md-6'>
							<Button className='mt-2 w-100' variant='book' type='submit'>
								 
							</Button>
						</div>
					</div>
				</Form>
			</Modal.Body>
		</Modal>
	)
}

export default EditPasswordModal
