import React from 'react'
import { Card, ListGroup } from 'react-bootstrap'
import CardTitle from '../common/CardTitle'
import booksitoutIcon from '../common/icons/booksitoutIcon';

const PwaNotSupportedFunctionCard = () => {
    const notSupportedFunctionList = ['  Apple Wallet ']

    return (
		<Card style={{ minHeight: '400px' }}>
			<Card.Body>
				<CardTitle icon={<booksitoutIcon.notSupported />} title={`   ( ${notSupportedFunctionList.length})`} />

				<div className='ps-4'>
					<ListGroup>
						{notSupportedFunctionList.map((notSupported) => {
							return <ListGroup.Item>{notSupported}</ListGroup.Item>
						})}
					</ListGroup>
				</div>
			</Card.Body>
		</Card>
	)
}

export default PwaNotSupportedFunctionCard
