import React from 'react'
import toast from 'react-hot-toast'
import { Modal, Form, Button } from 'react-bootstrap'

import { addReview } from '../../functions/book'

const AddReviewModal = ({ isModalOpen, setIsModalOpen, book, setBook }) => {
	const [review, setReview] = React.useState('')

	const handleAddReview = (e) => {
		e.preventDefault()

		if (review === '') {
			toast.dismiss()
			toast.error('  ')
			return
		}

		addReview(book.bookId, review).then((success) => {
			if (success) {
				setBook({ ...book, review: review })
				setIsModalOpen(false)
				toast.success(' ')
			} else {
				toast.error(' .     ')
			}
		})
	}

	return (
		<Modal show={isModalOpen} fullscreen='md-down' onHide={() => setIsModalOpen(false)} backdrop='static' centered>
			<Modal.Header closeButton className='text-center' onHide={() => setIsModalOpen(false)}>
				<h4 className='w-100'>  </h4>
			</Modal.Header>

			<Modal.Body>
				<Form onSubmit={(e) => handleAddReview(e)}>
					<Form.Control as='textarea' placeholder='   ' onChange={(e) => setReview(e.target.value)} autoFocus />

					<Button variant='book' type='submit' className='mt-3 w-100'>
						
					</Button>
				</Form>
			</Modal.Body>
		</Modal>
	)
}

export default AddReviewModal
