export const MyBookSearchRange = {
    getApiKey: () => {
        return localStorage.getItem('search-my-book-range') ?? 'ALL'
    },

    getDisplayName: () => {
        const apiKey = MyBookSearchRange.getApiKey()

			if (apiKey === 'ONLY_READING') {
                return '  '
            }

			if (apiKey === 'ONLY_DONE') {
				return '  '
			}

			if (apiKey === 'EXCLUDE_GIVE_UP') {
                return '  '
            }

            return ''
    }
    
}