import React from 'react'
import toast from 'react-hot-toast'
import { Modal, Form, Button } from 'react-bootstrap'
import { addMemo } from '../../functions/memo'
import '../input.css'
import EditorForm from '../../common/editor/EditorForm'

const AddMemoModal = ({ modalOpen, setModalOpen, book, memoList, setMemoList }) => {
	const [page, setPage] = React.useState<number | null>(null)
	const [content, setContent] = React.useState<string | null>(null)

	const handleAddMemo = (e) => {
		e.preventDefault()

		if ((page ?? 1) < 0) {
			document.getElementById('page-input')!!.focus()
			toast.error(' 0   ')
			return
		}

		if ((page ?? 0) > book.endPage) {
			document.getElementById('page-input')!!.focus()
			toast.error('     ')
			return
		}

		if (content == null || content === '') {
			document.getElementById('content-input')!!.focus()
			toast.error('  ')
			return
		}

		const newMemo = {
			page: page ?? null,
			content: content,
		}

		addMemo(newMemo, book.id).then((success) => {
			if (success) {
				toast.success(' ')
				setMemoList([...memoList, newMemo])
				setPage(null)
				setContent(null)
				setModalOpen(false)
			} else {
				toast.error('      ')
			}
		})
	}

	return (
		<Modal show={modalOpen} onHide={() => setModalOpen(false)} fullscreen='md-down' centered size='xl'>
			<Modal.Header closeButton className='text-center'>
				<h4 className='w-100'>  </h4>
			</Modal.Header>

			<Modal.Body>
				<Form onSubmit={(e) => handleAddMemo(e)}>
					<Form.Label></Form.Label>
					<EditorForm placeholder={'  '} setContent={setContent} id='content-input' />
					<div className='mb-2' />

					<Form.Label type='number'></Form.Label>
					<Form.Control
						type='number'
						inputMode='numeric'
						pattern='[0-9]*'
						onChange={(e) => setPage(Number(e.target.value))}
						id='page-input'
					/>
					<div className='mb-2' />

					<div className='row'>
						<div className='col-12 col-md-6'>
							<Button variant='book-danger' className='w-100 mt-3' onClick={() => setModalOpen(false)}>
								
							</Button>
						</div>

						<div className='col-12 col-md-6'>
							<Button variant='book' type='submit' className='w-100 mt-3'>
								
							</Button>
						</div>
					</div>
				</Form>
			</Modal.Body>
		</Modal>
	)
}

export default AddMemoModal
