			<Modal.Header closeButton className=" text-center">
				<h2 className="w-100"> / </h2>
			</Modal.Header>

			<Modal.Body>
				<TextContainer>
					<Text>,        .</Text>
					<Text>    .</Text>
					<Text>      .</Text>
				</TextContainer>

				<ButtonContainer>
					<Button variant="book" className="col-12 col-md-6" onClick={() => setShow(false)}>
						
					</Button>
